import * as React from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import NumberFinder from '../components/NumberFinder'
import useAuth from "../hooks/userAuth"
import { StaticImage } from "gatsby-plugin-image"
import { Form, Button } from '@themesberg/react-bootstrap'
import Parse from '../vendor/Parse'
import { useLocation } from "@reach/router"
import Header from '../components/Head'
const useQueryParams = () => {
    const { search } = useLocation()
    return React.useMemo(() => new URLSearchParams(search), [search])
}

const IndexPage = () => {
    const { isLogin } = useAuth()
    const queryParams = useQueryParams()

    const [numbers, setNumbers] = React.useState(["", "", ""])
    const [email, setEmail] = React.useState("")
    const [contact, setContact] = React.useState("")
    const [errors, setErrors] = React.useState({ email: "", contact: "" })
    const [submitting, setSubmitting] = React.useState(false)

    // Populate from query string
    React.useEffect(() => {
        const nums = [
            queryParams.get("num1") || "",
            queryParams.get("num2") || "",
            queryParams.get("num3") || ""
        ]
        setNumbers(nums)

        const qEmail = queryParams.get("email")
        const qContact = queryParams.get("contact")

        if (qEmail) setEmail(qEmail)
        if (qContact) setContact(qContact)
    }, [queryParams])

    const validate = () => {
        let valid = true
        const newErrors = { email: "", contact: "" }

        if (!email.match(/^[^\s@]+@[^\s@]+\.[^\s@]+$/)) {
            newErrors.email = "Invalid email address"
            valid = false
        }

        if (!contact.match(/^\d{8,15}$/)) {
            newErrors.contact = "Contact number should be 8–15 digits"
            valid = false
        }

        setErrors(newErrors)
        return valid
    }

    const handleMonitorButton = async () => {
        if (!validate()) return

        setSubmitting(true)
        try {
            const res = await Parse.Cloud.run('saveUserMonitoring', {
                numbers,
                email,
                contact
            })
            alert("Submitted successfully!")
        } catch (err) {
            console.error(err)
            alert("Something went wrong.")
        }
        setSubmitting(false)
    }

    return (
        <Layout afterSearch={<></>}>
            <Header />

            <div style={{ textAlign: "center", maxWidth: 500, margin: "auto" }}>
                <Form>
                    {numbers.map((num, i) => (
                        <Form.Group className="mb-2" key={i}>
                            <Form.Control
                                type="text"
                                placeholder={`Enter number ${i + 1}`}
                                value={num}
                                onChange={(e) => {
                                    const updated = [...numbers]
                                    updated[i] = e.target.value
                                    setNumbers(updated)
                                }}
                            />
                        </Form.Group>
                    ))}
                    <Form.Group className="mb-2">
                        <Form.Control
                            type="email"
                            placeholder="Your email"
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            isInvalid={!!errors.email}
                            required
                        />
                        <Form.Control.Feedback type="invalid">
                            {errors.email}
                        </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Control
                            type="text"
                            placeholder="Your contact number"
                            value={contact}
                            onChange={(e) => setContact(e.target.value)}
                            isInvalid={!!errors.contact}
                            required
                        />
                        <Form.Control.Feedback type="invalid">
                            {errors.contact}
                        </Form.Control.Feedback>
                    </Form.Group>
                    <Button
                        variant="primary"
                        onClick={handleMonitorButton}
                        disabled={submitting}
                    >
                        {submitting ? "Submitting..." : "Please Notify Me When Number is Detected"}
                    </Button>
                </Form>
            </div>
        </Layout>
    )
}

export const Head = () => <Seo title="Home" />

export default IndexPage
